@import url('https://fonts.googleapis.com/css2?family=Montagu+Slab:wght@500&family=Poppins:wght@300&display=swap');

body {
    font-family: Poppins, sans-serif; 
    background-color: dodgerblue;
    display: flex;
    align-items: center;
    flex-direction: column;
}

h1 {
    font-family: Montagu Slab, serif;
}

h2 {
    font-family: Montagu Slab, serif;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contentarea {
    width: auto;
    justify-content: center;
}

.nav {
    width: 100%;
    color: white;
}

.nav ul {
    list-style-type: none;
    overflow: hidden;
}

.nav li {
    display: inline;
    padding-left: 1em;
}

.nav h1 {
    padding: 0;
    margin: 0;
    display: inline;
}

.nav a {
    color: white; 
}

.nav a:hover {
    color: powderblue; 
}

.nav button {
  background: none!important;
  border: none;
  padding: 0!important;
  font-size: 1em;
  font-family: Poppins, sans-serif;
  text-decoration: underline;
  cursor: pointer;
  color: white; 
}

.nav button:hover {
  color: powderblue; 
}

.links li {
  padding-left: 1em;
}

.card {
    background-color: #6bb6ff;
    color: #111f2d;
    border-radius: 1em;
    padding-left: 2em;
    padding-right: 2em;
    padding-top: 1em;
    padding-bottom: 1em;
    margin: 1em;
    min-width: 16em;
}

